<template>
  <!--지점선택-->
  <ul class="chatStoreType01">
    <li
      v-for="(item, index) in data.list"
      :key="index"
    >
      <a
        @click="click(item)"
        :class="isOnConsulting ? 'cb-disabled-button' : ''"
        role="button"
      >
        {{ item.name }}
      </a></li>
  </ul>
  <!--//지점선택-->
</template>

<script>

import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'BranchSelection',
  mixins: [consultingStatus],
  props: {
    intent: String,
    data: Object,
  },
  methods: {
    click({
      message, tag, replace,
    }) {
      if (this.isOnConsulting) {
        return;
      }

      this.$analyticsHandler.apply(tag);

      this.$store.dispatch('SEND_MESSAGE', {
        message,
        options: replace ? { replace } : {},
      });
    },
  },
};
</script>